/* General Reset */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #EBEBEB; /* Light grey background */
  color: #333333; /* Dark grey text */
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Ensure background covers full screen on mobile */
  min-height: 100vh;
  min-height: -webkit-fill-available;
  /* Handle safe areas for notched devices */
  padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);
}

a {
  color: #333333; /* Dark grey for links */
  text-decoration: underline;
}

a:hover {
  text-decoration: none;
}

/* App Container */
.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  /* Ensure content doesn't overlap with safe areas */
  padding-left: max(20px, env(safe-area-inset-left));
  padding-right: max(20px, env(safe-area-inset-right));
  padding-top: max(40px, env(safe-area-inset-top));
  padding-bottom: max(40px, env(safe-area-inset-bottom));
}

/* Header Styles */
.App-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
  animation: fadeInDown 0.8s ease-out;
}

.name {
  font-size: 2rem;
  font-weight: bold;
  margin: 0;
  color: #333333;
}

/* Navigation */
nav {
  display: flex;
  gap: 20px;
}

nav a {
  font-size: 1rem;
  color: #333333;
  text-decoration: none;
}

nav a:hover {
  text-decoration: underline;
}

/* Section Styles */
.section {
  margin: 40px 0;
  text-align: left;
}

.section h2 {
  font-size: 1.3rem;
  font-weight: bold;
  margin: 40px 0 20px 0;
  color: #333333;
  border-bottom: none;
  display: block;
  padding-bottom: 0;
}

.section p {
  font-size: 1.1rem;
  color: #333333;
  margin-bottom: 20px;
  line-height: 1.6;
  opacity: 0;
  animation: fadeInUp 0.6s ease-out forwards;
}

/* Waterfall cascade animation for paragraphs */
.section p:nth-child(1) { animation-delay: 0.2s; }
.section p:nth-child(2) { animation-delay: 0.4s; }
.section p:nth-child(3) { animation-delay: 0.6s; }
.section p:nth-child(4) { animation-delay: 0.8s; }
.section p:nth-child(5) { animation-delay: 1.0s; }
.section p:nth-child(6) { animation-delay: 1.2s; }

.project {
  margin: 20px 0;
  background: transparent;
  padding: 0;
  border-radius: 0;
  box-shadow: none;
}

.project h3 {
  margin: 0 0 10px 0;
  color: #333333;
  font-size: 1.1rem;
  font-weight: normal;
}

.project p {
  margin: 0;
  color: #333333;
}

/* Footer Styles */
.App-footer {
  margin-top: 60px;
  padding: 20px 0;
  border-top: none;
  color: #333333;
  text-align: left;
  animation: fadeInUp 0.8s ease-out 1.4s both;
}

/* Animation Keyframes */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Smooth page load animation */
.App {
  animation: pageLoad 0.5s ease-out;
}

@keyframes pageLoad {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Mobile-specific adjustments */
@media screen and (max-width: 768px) {
  .App {
    padding: 20px 15px;
    padding-left: max(15px, env(safe-area-inset-left));
    padding-right: max(15px, env(safe-area-inset-right));
    padding-top: max(20px, env(safe-area-inset-top));
    padding-bottom: max(20px, env(safe-area-inset-bottom));
  }
  
  .name {
    font-size: 1.8rem;
  }
  
  .section p {
    font-size: 1rem;
  }
}
